<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.image" :alt="product.item">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat}}
    </span>

    <ul class="colors colors--black">
      <li v-for="color in product.colors"  :key="color.id" class="colors__item">
        <label :for="color.id" class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="color.id" :id="color.id" v-model.number="color.code">
          <span class="colors__value" :style="{'background-color': color.code}">
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import goToPage from '@/helpers/gotoPage'
import numberFormat from '@/helpers/numberFormat';
  export default {
    props: ['product', 'colorId'],
    filters: {
      numberFormat
    },
    methods: {
      goToPage
    }
  }

</script>